// * Стили компонента Buton (Load more)

.ButtonLoadMore {
    padding: 8px 16px;
    border-radius: 2px;
    background-color: #3f51b5;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    text-align: center;
    display: inline-block;
    color: #fff;
    border: 0;
    text-decoration: none;
    cursor: pointer;
    font-family: inherit;
    font-size: 18px;
    line-height: 24px;
    font-style: normal;
    font-weight: 500;
    min-width: 180px;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
      0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
  }
  
  .ButtonLoadMore:hover,
  .ButtonLoadMore:focus {
    background-color: #303f9f;
  }
  
  .SearchFormButton {
    display: inline-block;
    width: 48px;
    height: 48px;
    border: 0;
    background-image: url('https://image.flaticon.com/icons/svg/149/149852.svg');
    background-size: 40%;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.6;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    outline: none;
  }
  
  .SearchFormButton:hover {
    opacity: 1;
  }
  
  .SearchFormButtonLabel {
    // position: absolute;
    // width: 1px;
    // height: 1px;
    padding: 5px;
    overflow: hidden;
    // clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    // clip-path: inset(50%);
    border: 0;
    font-weight: 900;
    font-style: italic;
    font-size: 15px;
  }